
import { ref } from "@vue/reactivity"
import { defineComponent, onMounted, PropType } from "@vue/runtime-core"
import { setUpTypes, PropsType, CardDataType } from "./types"

export default defineComponent({
  props: {
    atmosphere: {
      type: Object as PropType<PropsType>,
      required: true,
    },
  },
  setup(props): setUpTypes {
    const cardData = ref<Array<CardDataType>>([
      {
        img: require("@/assets/img/windy-icon@2x.png"),
        alt: " ",
        unit: "m/s",
        description: "",
        rate: 0,
      },
      {
        img: require("@/assets/img/rain-drop-icon@2x.png"),
        alt: " ",
        unit: "%",
        description: "",
        rate: 0,
      },
    ])

    const setWindDriection = (vec: number) => {
      if (vec) {
        if (22.5 < vec && vec <= 67.5) return ""
        else if (67.5 < vec && vec <= 112.5) return ""
        else if (112.5 < vec && vec <= 157.5) return ""
        else if (157.5 < vec && vec <= 202.5) return ""
        else if (202.5 < vec && vec <= 247.5) return ""
        else if (247.5 < vec && vec <= 292.5) return ""
        else if (292.5 < vec && vec <= 337.5) return ""
        else if (337.5 < vec && vec <= 22.5) return ""
      }
      return ""
    }

    onMounted(() => {
      cardData.value[1].rate = props.atmosphere.reh
      cardData.value[0].rate = props.atmosphere.wsd
      cardData.value[0].description = setWindDriection(props.atmosphere.vec)
    })

    return { cardData }
  },
})
